<template>
  <div class="populate">
    <Navbar />
    <div class="below_navbar">
      <div class="kiri">
        <div class="cart">
          <table class="pure-table">
            <thead>
              <th>No</th>
              <th>Makanan</th>
              <th>Harga</th>
              <th>Qty</th>
              <th>Sub Total</th>
              <th>&nbsp;</th>
            </thead>
            <tbody>
              <tr v-for="(item, index) in get_cart" :key="item" class="cart">
                <td>{{ index + 1 }}</td>
                <td>{{ item.makanan }}</td>
                <td>{{ item.harga }}</td>
                <td>{{ item.qty }}</td>
                <td>{{ item.sub_tot }}</td>
                <td @click="remove(index)">X</td>
              </tr>
            </tbody>
          </table>
        </div>
        <div class="totalpay">
          <div class="total">
            <h2>Total : {{ get_total_cart }}</h2>
          </div>
          <div class="paycancel">
            <div @click="cancel_invoice" class="cancel">
              <h3>Cancel Invoice</h3>
            </div>
            <div @click="proceed_payment(get_total_cart)" class="pay">
              <h3>Proceed Payment</h3>
            </div>
          </div>
        </div>
      </div>
      <div class="kanan">
        <div v-for="prod in products" :key="prod" class="cards">
          <div class="card_image">
            <img
              v-bind:src="'http://127.0.0.1:5001/get_image/' + prod[2]"
              alt=""
            />
          </div>
          <div class="food_name">
            <p>{{ prod[0] }}</p>
            <p>Rp.{{ prod[1] }}</p>
          </div>

          <div class="food_add">
            <input type="text" ref="qtyInput" v-model="prod.food_qty" />
            <div class="add" @click="add(prod)">Add</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Navbar from "@/components/Navbar.vue";
export default {
  name: "Cashier",
  components: {
    Navbar,
  },
  data() {
    return {
      food_qty: "",
      cart: [],
    };
  },
  methods: {
    add(val) {
      if (val.food_qty > 0) {
        let makanan = val[0];
        let harga = val[1];
        let qty = parseInt(val.food_qty);
        let sub_tot = harga * qty;
        let check_cart = (obj) => obj.makanan == makanan;
        let already_in_cart = this.cart.some(check_cart);
        if (already_in_cart == true) {
          let item_index = this.cart.findIndex((obj) => obj.makanan == makanan);
          this.cart[item_index].qty += qty;
          this.cart[item_index].sub_tot += sub_tot;
        } else {
          let to_push = {
            makanan: makanan,
            harga: harga,
            qty: qty,
            sub_tot: sub_tot,
          };
          this.cart.push(to_push);
        }

        val.food_qty = null;
      }
    },
    remove(val) {
      this.cart.splice(val, 1);
    },
    cancel_invoice() {
      this.cart = [];
    },
    proceed_payment(val) {
      let new_invoice = { total: val };
      axios
        .post("http://127.0.0.1:5000/post_invoice", new_invoice)
        .then((response) => {});

      // axios
      //   .post("https://127.0.0.1:5000/post_solds", this.cart)
      //   .then((response) => {
      //     console.log(this.cart);
      //     console.log(response);
      //   });

      let items_transaction = "Telah terjadi transaksi :\n \n";

      this.cart.forEach(function (sold) {
        let temp = `- ${sold.makanan} (${sold.harga}), sebanyak ${sold.qty} porsi. Sub Total : Rp. ${sold.sub_tot}. \n`;
        items_transaction += temp;
      });
      items_transaction += `\n Total transaksi : Rp. ${this.get_total_cart}`;

      let email_to_report = prompt("Kirimkan invoice ke alamat email:");
      if (email_to_report == null) {
        alert("Tidak mengirimkan invoice, terima kasih!");
        this.cart = [];
      } else {
        let email_data = {
          subject: "My Chicken Transaction Report",
          to: email_to_report,
          body: items_transaction,
          key: "ibda3211-yehezkielpw",
        };
        axios
          .post("http://127.0.0.1:5002/send_mail", email_data)
          .then((response) => {});
        alert("Invoice dikirimkan, terima kasih!");
      }
      this.cart = [];
    },
  },
  computed: {
    products() {
      return this.$store.state.products;
    },
    img(word) {
      let url = "http://127.0.0.1:5001/get_image/" + word;
      return url;
    },
    get_cart() {
      return this.cart;
    },
    get_total_cart() {
      let total = this.cart.reduce(function (accumulator, item) {
        return accumulator + item.sub_tot;
      }, 0);
      return total;
    },
  },
  mounted() {
    this.$store.dispatch("fetch_products");
  },
};
</script>

