<template>
  <div class="populate">
    <div class="box">
      <div class="logo">
        <img src="../assets/logo.png" alt="" />
      </div>
      <div class="id_pw">
        <div class="id_pw_kiri">
          <div class="input_name">
            <h1>ID</h1>
            <h1>:</h1>
          </div>
          <div class="input_name">
            <h1>Password</h1>
            <h1>:</h1>
          </div>
        </div>
        <div class="id_pw_kanan">
          <input type="text" v-model="id" />
          <input type="password" v-model="password" />
        </div>
      </div>
      <div class="submit">
        <div v-if="login_error" class="error">
          <h3>{{ login_message }}</h3>
        </div>
        <div class="button" v-on:click="login()">
          <h1>Login</h1>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import VueCookies from "vue-cookies";
export default {
  name: "Login",
  data() {
    return {
      id: null,
      password: null,
      login_error: false,
      login_message: null,
    };
  },
  methods: {
    login() {
      let id_pw = {
        id: this.id,
        password: this.password,
      };
      axios
        .post("https://main-api.yehezkiel.pw/login", id_pw)
        .then((response) => {
          if ("error" in response.data) {
            this.login_error = true;
            this.login_message = response.data["error"];
            this.$router.push({ path: "/login" });
          } else {
            let jwt = response.data["access_token"];
            let id_logined = response.data["id"];
            let role_logined = response.data["role"];
            VueCookies.set("jwt", jwt, "1h");
            VueCookies.set("id", id_logined, "1h");
            VueCookies.set("role", role_logined, "1h");
            this.$router.push({ path: "/" });
          }
        });
    },
  },
};
</script>



<style scoped>
.box {
  width: 35%;
  height: 85%;
  background-color: #fbf2e5;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  border: 4px solid black;
  border-radius: 7.5px;
}

.logo {
  width: 100%;
  height: 35%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.logo img {
  height: 100%;
}

.id_pw {
  width: 100%;
  height: 35%;
  display: flex;
}

.id_pw_kiri {
  width: 40%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.id_pw_kiri h1 {
  margin-left: 5%;
}
.input_name {
  display: flex;
  justify-content: space-between;
}
.id_pw_kanan {
  width: 57.5%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.id_pw_kanan input {
  height: 20%;
  width: 95%;
}
.submit {
  width: 100%;
  height: 35%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  justify-content: space-evenly;
}
.error {
  width: 100%;
  height: 20%;
}
.error h3 {
  text-align: center;
  color: red;
}

.button {
  width: 50%;
  height: 30%;
  background-color: gainsboro;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  border: 2px solid black;
}
.button h1 {
  color: black;
}
.button:hover {
  background-color: gray;
  cursor: pointer;
}
</style>
