import axios from 'axios';
import { createStore } from 'vuex'

let main_api = "https://127.0.0.1:5000";
let image_api = "https://127.0.0.1:5001"

export default createStore({
  state: {
    products: null,

  },
  mutations: {
    fetch_products(state, obj) {
      state.products = obj
    },
    clear_products(state) {
      state.products = null
    }

  },
  actions: {
    fetch_products({ commit }) {
      axios(main_api + "/get_products")
        .then(response => {
          commit('fetch_products', response.data)
        })
    },
    clear_products({ commit }) {
      commit('clear_products')
    },
  },
  getters: {

  },
  modules: {
  }
})


