<template>
  <div class="container">
    <div class="logo">
      <img src="../assets/logo.png" alt="" />
    </div>
    <div v-if="is_manager" class="navigation">
      <router-link class="nav_link" to="/">Cashier</router-link>
      <router-link class="nav_link" to="administration">Administration</router-link>
      <router-link class="nav_link" to="sales_report">Sales Report</router-link>
    </div>
    <div class="name_time">
      <div class="time">
        <h3>{{time}}</h3>
      </div>
      <div class="name">
        <h2>{{logged_id}}</h2>
        <div class="logout" v-on:click="logout()">Logout</div>
      </div>
    </div>
  </div>
</template>

<script>
import VueCookies from 'vue-cookies';

export default {

  data() {
    return {
      interval: null,
      time: null,
      logged_id : VueCookies.get("id"),
      is_manager: this.manager_check()
    };
  },
  methods : {
    manager_check(){
      let status = VueCookies.get('role');
      if(status == "manager"){
        return true
      }else{
        return false
      }
    
    },
    logout(){
        VueCookies.remove("jwt");
        VueCookies.remove("role");
        VueCookies.remove("id");
        this.$router.push({path:"/login"});
      
    }
    
  },
  beforeDestroy() {
    clearInterval(this.interval);
  },
  mounted() {
    this.interval = setInterval(() => {
      this.time = Intl.DateTimeFormat('id-GB',{ dateStyle: 'full', timeStyle: 'short' }).format();
    }, 1000);
  },
};
</script>


<style>
</style>

<style scoped>
.container {
  width: 100%;
  height: 15%;
  background-color: #fbf2e5;
  border-bottom: 2px solid black;
  display: flex;
  justify-content: space-evenly;
}
.logo {
  width: 20%;
  height: 100%;
  display: flex;
  justify-content: center;
}
.logo img {
  height: 100%;
}
.navigation {
  width: 60%;
  height: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.name_time {
  width: 20%;
  height: 100%;
}

.time{
  width: 100%;
  height: 35%;
  display: flex;
  justify-content: center;
  align-items: center;
}


.time h3{
  font-size: 100%;

}
.name{
  width: 100%;
  height: 65%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  justify-content: space-around;
}
.name h2{
  text-align: center;
  width: 50%;
  text-transform: capitalize;
  border-bottom: 2px solid black;
}
.logout{
  width: 50%;
  background-color: gainsboro;
  border: 1px solid black;
  border-radius: 10px;
  text-align: center;
}
.logout:hover{
  background-color: gray;
  cursor: pointer;
}
.nav_link{
  border-radius: 10px;
  text-decoration: none;
  color: black;
  font-size: 4vh;
  width: 30%;
  text-align: center;
}

.nav_link:hover{
  cursor: pointer;
  background-color: gainsboro;
  border: 3px black solid;
}
</style>